.todo__li{
    padding: 20px 10px;
    border-bottom: 1px solid #8d8d8d;
    display: flow-root;
    .todo___text{
        width: 70vw;
        text-decoration: none;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        float: left;
        font-size: x-large;
    }

    .btn__area{
        float: right;
        width: 10vw;
    }
}

